(function () {
    'use strict';

    /*@ngInject*/
    function UserStorage ($rootScope, LocalStorageService) {


        this.setLoginData = function(data){
            $rootScope.auth = data;
            LocalStorageService.setObject('auth', data);
        };

        this.clearLoginData = function(){
            LocalStorageService.remove('auth');
            $rootScope.auth = null;
        };

        this.getLoginData = function(){
            return this.getAuth===null?null:$rootScope.auth;
        };

        this.getAuth = function(){
            if(angular.isDefined(LocalStorageService.get('auth'))){
                return LocalStorageService.getObject('auth');
            }else{
                return null;
            }
        };

    }
    angular.module('orcamentoBoxFrontend')
        .service('UserStorage', UserStorage);

})();


(function () {
    'use strict';

    /*@ngInject*/
    function Slugfy() {
        return function (str) {
            str = str.replace(/^\s+|\s+$/g, ''); // trim
            str = str.toLowerCase();

            // remove accents, swap ñ for n, etc
            var from = "àáäâèéëêìíïîòóöôùúüûñç·/_,:;";
            var to = "aaaaeeeeiiiioooouuuunc------";

            for (var i = 0, l = from.length; i < l; i++)
                str = str.replace(new RegExp(from.charAt(i), 'g'), to.charAt(i));

            str = str.replace(/[^a-z0-9 -]/g, '') // remove invalid chars
                .replace(/\s+/g, '-') // collapse whitespace and replace by -
                .replace(/-+/g, '-'); // collapse dashes

            return str;
        }
    }

    angular.module('orcamentoBoxFrontend')
        .filter('slugfy', Slugfy);

})();


