(function () {
  'use strict';

  angular
    .module('orcamentoBoxFrontend')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($rootScope, $window, $state, $timeout, $filter, LocalStorageService) {
    var vm = this;

    vm.search = {
      autocomplete: null,
      details: null,
      type: null,
      side_a: null,
      side_b: null,
      side_c: null,
      options: {
        country: 'br'
      }
    };

    //Home height based on visible area of browser
    vm.homeHeight = {
      height: ($window.innerHeight - document.getElementsByClassName('header')[0].offsetHeight) + 'px'
    };
    vm.homeWidth = {
      width: $window.innerWidth + 'px'
    };

    vm.steps = [{
      'width': vm.homeWidth.width,
      'transform': 'translateX(0)'
    }, {
      'width': vm.homeWidth.width,
      'transform': 'translateX(0)'
    }, {
      'width': vm.homeWidth.width,
      'transform': 'translateX(0)'
    }, {
      'width': vm.homeWidth.width,
      'transform': 'translateX(0)'
    }];

    vm.setStep = function (step) {
      for (var i = 0; i <= vm.steps.length - 1; i++) {
        vm.steps[i]['transform'] = 'translateX(-' + String(parseInt(vm.homeWidth.width.replace(/\D/g, '') * step)) + 'px)';
      }
      if (step === 3) {
        $rootScope.search = vm.search;
        LocalStorageService.setObject('search', vm.search);

        var city = vm.search.details.address_components.filter(function(o) {
          for(i in o.types) {
            if(o.types[i] === 'locality') {
              return o;
            }
          }
        });

        var searhParams = {city:$filter('slugfy')(city[0].long_name),type: vm.search.type};

        if(vm.search.side_a !== null){
          angular.extend(searhParams, {side_a: vm.search.side_a});
        }

        if(vm.search.side_b !== null){
          angular.extend(searhParams, {side_b: vm.search.side_b});
        }

        if(vm.search.side_c !== null){
          angular.extend(searhParams, {side_c: vm.search.side_c});
        }


        $timeout(function(){
          $state.go('root.search', searhParams);
        }, 1000);
      }

    };

    $rootScope.metatags.title = 'Orçamentos de Box Online, encontre os melhores preços, peto de você | Orçamento BOX';
  }
})();

