(function() {
  'use strict';

  angular
    .module('orcamentoBoxFrontend')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, $location, UserStorage) {
    $rootScope.metatags = {
      title: 'Orçamentos de Box Online, encontre os melhores preços, perto de você | Orçamento BOX',
      description: 'Busque OnLine e encontre os melhores orçamentos de box mais próximos de você, rápido e sem complicação',
      robots: 'index, follow',
      keywords: 'Orçamentos, Box, Online',
      canonical: 'http://www.orcamentobox.com.br/',
      fb_app_id: '',
      fb_url: 'http://www.orcamentobox.com.br/',
      fb_title: 'Orçamentos de Box Online, encontre os melhores preços, perto de você | Orçamento BOX',
      fb_image: '',
      fb_description: 'Busque OnLine e encontre os melhores orçamentos de box mais próximos de você, rápido e sem complicação',
      fb_site_name: 'Orçamento Box',
      fb_type: 'website',
      code: 200,
      newpageUrl: ''
    };
    $log.debug('runBlock end');


    $rootScope.$on('$stateChangeStart', function (event, toState, toParams) {

      var auth = UserStorage.getAuth();

      $rootScope.checkMenu = false;

      if(auth && (toState.name === 'auth.signUp' || toState.name === 'auth.login')){
        $location.path('/');
      }

      if (toState.authenticate && auth === null) {
        //UserStorage.clearLoginData();
        $rootScope.redirectTo = {name: toState.name, param: toParams};
        $location.path('/conta/entrar');
      }
    });

  }

})();

(function () {
    'use strict';

    angular
        .module('orcamentoBoxFrontend')
        .config(routerConfig);

    /** @ngInject */
    function routerConfig($stateProvider, $urlRouterProvider) {

        var header = {
            templateUrl: 'app/components/navbar/navbar.html',
            controller: 'NavbarController',
            controllerAs: 'header'
        };

        var footer = {
            templateUrl: 'app/components/footer/footer.html',
            controller: 'FooterController',
            controllerAs: 'footer'
        };


        $stateProvider

            .state('root', {
                abstract: true,
                deepStateRedirect: true,
                'views': {
                    'header': header,
                    'container@': {
                        template: '<ui-view/>'
                    },
                    'footer': footer
                }
            })

            .state('root.home', {
                url: '/',
                views: {
                    'container@': {
                        templateUrl: 'app/main/main.html',
                        controller: 'MainController',
                        controllerAs: 'main'
                    }
                }
            })
            .state('root.howitworks', {
                url: '/como-funciona',
                authenticate: false,
                views: {
                    'container@': {
                        templateUrl: 'app/how-it-works/how-it-works.html',
                        controller: 'HowController',
                        controllerAs: 'how'
                    }
                }
            })
            .state('root.joinus', {
                url: '/junte-se',
                authenticate: false,
                views: {
                    'container@': {
                        templateUrl: 'app/join-us/join-us.html',
                        controller: 'JoinController',
                        controllerAs: 'join'
                    }
                }
            })
            .state('root.contact', {
                url: '/contato',
                authenticate: false,
                views: {
                    'container@': {
                        templateUrl: 'app/contact/contact.html',
                        controller: 'ContactController',
                        controllerAs: 'contact'
                    }
                }
            })

            .state('root.search', {
                url: '/busca/:city?side_a,side_b,side_c',
                params: {
                    city: null,
                    side_a: null,
                    side_b: null,
                    side_c: null
                },
                authenticate: false,
                views: {
                    'container@': {
                        templateUrl: 'app/search/search.html',
                        controller: 'SearchController',
                        controllerAs: 'search'
                    }
                }
            })

            .state('root.askForAgent', {
                url: '/solicitar-visita/:id',
                params: {
                    id: null
                },
                authenticate: true,
                views: {
                    'container@': {
                        templateUrl: 'app/ask-agent/ask-agent.html',
                        controller: 'AgentController',
                        controllerAs: 'agent'
                    }
                }
            })


            .state('root.generic', {
                url: '/:page',
                authenticate: false,
                resolve: {},
                views: {
                    'container@': {
                        templateUrl: 'app/city/home.html'
                    }
                }
            })

            .state('root.account', {
                abstract: true,
                deepStateRedirect: true,
                authenticate: false,
                url: '/conta',
                'views': {
                    'header': header,
                    'container@': {
                        template: '<ui-view/>'
                    },
                    'footer': footer
                }
            })
            .state('root.account.login', {
                url: '/entrar',
                authenticate: false,
                views: {
                    'container@': {
                        templateUrl: 'app/account/login/login.html',
                        controller: 'LoginController',
                        controllerAs: 'login'
                    }
                }
            })
            .state('root.account.signup', {
                url: '/cadastrar',
                authenticate: false,
                views: {
                    'container@': {
                        templateUrl: 'app/account/signup/signup.html',
                        controller: 'SignupController',
                        controllerAs: 'signup'
                    }
                }
            });

        $urlRouterProvider.otherwise('/');
    }

})();

